import React from 'react';
import { Provider } from 'react-redux';

import { AnimateableSwitch, usePresenceRemoval } from '@mssgme/ui';
import { CollectionView, ProductView } from '../../Product';
import { PaymentStatusPage, UserOrderPage, UserOrdersPage } from '../../Order';
import { useCoreContext } from '../../../hooks';

const ordersRouting = {
    component: UserOrdersPage,
    exact: true,
    children: {
        ':orderId': {
            component: UserOrderPage,
            children: {
                'payment/:status': PaymentStatusPage,
            },
        },
    },
};

const productRouting = {
    component: ProductView,
    children: {
        orders: ordersRouting,
    },
};

const routing = {
    'p/:productSlug': productRouting,
    'c/:collectionSlug': {
        component: CollectionView,
        children: {
            'p/:productSlug': productRouting,
        },
    },
    'orders': ordersRouting,
};

export function PageProductsRouting(props) {
    const { store } = useCoreContext();

    usePresenceRemoval();

    const content = <AnimateableSwitch routing={routing} {...props} />;

    return store ? <Provider store={store}>{content}</Provider> : content;
}
